.parent{
    margin-top: 50px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
    text-align: center;
    margin-bottom: 25px;

}
.card{
    border-radius: 10px;
    box-shadow: 0 0 15px 0.5px rgb(233, 231, 231);
    text-align: center;
    align-items: center;
    width: 300px;
    height: 500px;
}
.img{
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    width: 100%;
    height: 300px;
}
.btn{
    width: 170px;
    color: #fff;
    height: 38px;
    margin-bottom: 10px;
    border-radius: 10px;
  
}
.p{
    font-size: large;
    color: red;
    font-weight: 700;
}
.h1{
    font-size: 20px;
    color: rgb(57, 57, 57);
}
.btn1{
    text-align: center;
    font-size: 40px;
}