.foot{
    width: 100%;
    height: 15vh;
   margin-top: 20px;
   margin-bottom: -45px;
    display: flex;
    /* justify-content: space-between; */
}
.foot_btn{
    text-align: center !important;
    justify-content: center !important;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 20px;
    align-items: center;
    padding-top: 40px;
    
}

.cardlarr{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    margin: 20px;

}
.cardd h5{
    margin-top: 35px;

}
.kartaa{
    margin-top: 25px;
}




.footer{
    height: 300px;
    background: url(https://64.media.tumblr.com/e0d747df3c0e0399dccd9dc8f1418aa7/b7b1684d4cbd56c8-1e/s1280x1920/1bd9faf6897d5239569f7fd758072bd9b4bc6f26.gif) no-repeat center center/ cover;
}
i{
    color: rgb(7, 93, 146);
}
.cardd{
    width:300px;
    box-shadow: 3px 3px rgb(7, 25, 128), -1em 0 .4em rgb(17, 95, 185);
    height: 300px;
    padding: 50px 15px;
    border-radius: 25%;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-top: 15px;

   
}

@media only screen and (max-width: 840px) {
.foot_btn{
   
    display: grid;
    grid-template-columns: repeat(1,1fr);  
}
.footer{
    width: 100%;
    height: 780px;
    margin-top: 15px;

    /* justify-content: space-between; */
}
.cardlarr{
    display: grid;
    grid-template-columns: repeat(1,1fr);
    /* margin: 20px; */
  text-align: center;
  align-items: center;

}
}

@media only screen and (max-width: 1300px) {
.cardlarr{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    margin: auto;

}
}

@media only screen and (max-width: 886px) {
.cardlarr{
    display: grid;
 justify-content: center;
 text-align: center;
    grid-template-columns: repeat(1,1fr);
margin: auto;


}
}



@media only screen and (max-width: 858px) {
.cardlarr{
    display: grid;
    grid-template-columns: repeat(2,1fr);
margin-left: -15px;


}
}

@media only screen and (max-width: 685px) {
.cardlarr{
    
    grid-template-columns: repeat(1,1fr);
margin-left: 70px;


}
}

